* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

li {
  list-style: none;
}

.list {
  max-width: 1920px;
  width: calc(100% - 20px);
  margin: 20px auto;
  background-color: tomato;
  text-align: center;
  height: 100vh;
}

.list h2 {
  padding-top: 50px;
  font-size: 100px;
}

.list form input {
  margin-top: 50px;
  width: 500px;
  height: 50px;
}

.list form button {
  width: 500px;
  height: 30px;
  margin: 0 auto;
  display: block;
  cursor: pointer;
  background-color: #fff;
  border: 0;
}

.list ul {
  margin-top: 10px;
}

.list ul li {
  width: 500px;
  margin: 0 auto;
  color: #000;
  position: relative;
  background-color: #fff;
  border: 1px dotted #000;
}

.list ul li:nth-child(odd)::before {
  content: '🥽';
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}

.list ul li:nth-child(even)::before {
  content: '🎁';
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}

.list ul li span::before {
  content: '🧨';
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  z-index: 99;
}



/* 
nth-child(2n) => 2배수
nth-child(odd) => 홀수
nth-child(even) => 짝수
*/